import React from 'react';
import styled from 'styled-components';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';

import {Link} from 'react-router-dom';

const LogoWrapper = styled.div`
    font-family: 'Londrina Outline', cursive;
    font-weight: 700;
    font-size: 40px;
`;

const LinkWrapper = styled.div`
    padding: 5px 15px; 

    &:hover{
        background-color: rgb(209, 220, 240);
    }
`;

const NavCollapseWrapper = styled.div`
    &.dropdown-menu {
        min-width: 11rem;
    }
`;



const Header = ({title}) => {
    document.title = title;
    return(
        <Navbar bg="light" expand="lg">
            <Navbar.Brand>
                <LogoWrapper>
                    <Link to='/'> Movies DB</Link>   
                </LogoWrapper>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <NavCollapseWrapper>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <NavDropdown title="" id="basic-nav-dropdown">
                            <LinkWrapper>
                                <Link to='/movie/popular/'></Link>
                            </LinkWrapper>
                            <LinkWrapper>
                                <Link to='/movie/playing-now/'> </Link>
                            </LinkWrapper>
                            <LinkWrapper>
                                <Link to='/movie/upcoming/'></Link>
                            </LinkWrapper>
                            <LinkWrapper>
                                <Link to='/movie/top-rated/'></Link>
                            </LinkWrapper>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </NavCollapseWrapper>
            <NavCollapseWrapper>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <NavDropdown title="" id="basic-nav-dropdown">
                            <LinkWrapper>
                                <Link to='/tv/popular/'></Link>
                            </LinkWrapper>
                            <LinkWrapper>
                                <Link to='/tv/airing_today/'>  </Link>
                            </LinkWrapper>
                            <LinkWrapper>
                                <Link to='/tv/on_the_air/'> </Link>
                            </LinkWrapper>
                            <LinkWrapper>
                                <Link to='/tv/top-rated/'></Link>
                            </LinkWrapper>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </NavCollapseWrapper>
            <NavCollapseWrapper>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <NavDropdown title="" id="basic-nav-dropdown">
                            <LinkWrapper>
                                <Link to='/person/'> </Link>
                            </LinkWrapper>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </NavCollapseWrapper>
        </Navbar> 
    )
}

export default Header;